@font-face {
    font-family: 'mont_boldbold';
    src: url('mont-bold-webfont.eot');
    src: url('mont-bold-webfont.eot?#iefix') format('embedded-opentype'),
         url('mont-bold-webfont.woff2') format('woff2'),
         url('mont-bold-webfont.woff') format('woff'),
         url('mont-bold-webfont.ttf') format('truetype'),
         url('mont-bold-webfont.svg#mont_boldbold') format('svg');
    font-weight: bold;
    font-style: normal;

}
@font-face {
    font-family: 'montbook';
    src: url('mont-book-webfont.eot');
    src: url('mont-book-webfont.eot?#iefix') format('embedded-opentype'),
         url('mont-book-webfont.woff2') format('woff2'),
         url('mont-book-webfont.woff') format('woff'),
         url('mont-book-webfont.ttf') format('truetype'),
         url('mont-book-webfont.svg#montbook') format('svg');
    font-weight: bold;
    font-style: normal;

}